  font-size: 0.875rem;
  background-image: url(../img/icons/arrow-left-red.svg);
  background-position: center left;
  background-repeat: no-repeat;
  background-size: 1rem 1rem;
  padding-left: 1.25rem;
}
.Link--forward {
  display: inline-block;
  min-height: 1rem;
  color: #e92b1e !important;
  font-size: 0.875rem;
  background-image: url(../img/icons/arrow-right-red.svg);
  background-position: center left;
  background-repeat: no-repeat;
  background-size: 1rem 1rem;
  padding-right: 1.25rem;
}
.Link--inverted {
  text-decoration: none;
  text-decoration-color: transparent;
  transition: color 0.25s linear, outline-offset 0.25s linear, text-decoration-color 0.25s linear;
}
.Link--inverted:focus, .Link--inverted:hover {
  text-decoration: underline;
  text-decoration-color: currentColor;
  text-decoration-thickness: 0.125rem;
}
.Link--invertedThin {
  text-decoration: none;
  text-decoration-color: transparent;
  transition: color 0.25s linear, outline-offset 0.25s linear, text-decoration-color 0.25s linear;
}
.Link--invertedThin:focus, .Link--invertedThin:hover {
  text-decoration: underline;
  text-decoration-color: currentColor;
}
.Link--stretched {
  white-space: normal;
}
.Link--stretched::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  pointer-events: auto;
}
.Link--overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  text-indent: -9999px;
  z-index: 1;
}

